.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-image: url('../../../assets/userFormbg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  padding: 3rem 1rem;
}

.img {
  display: none;
  /* background-color: rgba(255, 255, 255, 0.6);
  padding: 5rem 1rem;
  border-radius: 50%; */
}

.content {
  margin-bottom: 2rem;
}

.username {
  margin-bottom: 1.5rem;
}

.eventHandle {
  margin-bottom: 0.75rem;
}

.support {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.login {
  background-color: #e0afa0;
  box-shadow: none;
  color: #111142;
}

.signup,
.forgot {
  color: var(--secondary);
  text-decoration: none;
}

@media (min-width: 768px) {
  .wrapper {
    padding: 7.5rem 2rem;
  }
}

@media (min-width: 1024px) {
  .img {
    display: block;
  }
  .wrapper {
    padding: 3rem 2rem;
  }
}

@media (min-width: 1440px) {
  .wrapper {
    padding: 7.5rem;
  }
}
