.filter {
  display: flex;
  justify-content: space-between;
}

.search {
  width: 100%;
  height: 4rem;
}

.role {
  margin-left: 1.9375rem;
}

.head {
  padding: 1.875rem 1.5rem;
}

.listColumn:first-child {
  width: 60%;
}

.dropdown {
  width: fit-content;
}

.username {
  margin-top: 0.5rem;
}

.body {
  position: relative;
}

.listItem {
  border-top: 1px solid var(--default-lighter);
}

.load {
  position: absolute;
  left: 0;
  background-color: white;
  width: 100%;
  height: 100%;
}

.listItem:last-child {
  border-bottom: 1px solid var(--default-lighter);
}

.listColumn {
  padding: 1.5rem;
}

.listColumn:last-child {
  color: var(--default);
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.edit,
.del {
  position: relative;
  text-align: center;
  display: flex;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  padding: 0.3rem;
  color: var(--default);
  transition: all 0.1s ease-in;
}

.edit.disabled,
.del.disabled {
  opacity: 0.7;
  pointer-events: none;
}

.edit.disabled:hover,
.del.disabled:hover {
  cursor: no-drop;
  transform: translateY(0);
  box-shadow: none;
}

.edit.disabled:before,
.del.disabled:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  border: 2px solid white;
  border-radius: 50%;
  border-top-color: var(--primary);
  animation: rotate 2s linear infinite;
}

.edit:hover,
.del:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 1.25rem 0 var(--shadow);
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.del {
  margin-left: 1.5rem;
}

.notfound {
  position: absolute;
  top: 40%;
  text-align: center;
  width: 100%;
}

.notfoundimg {
  width: 150px;
  height: 150px;
}
