.container {
  position: relative;
  width: fit-content;
  width: 100%;
}

.label {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  color: var(--default-light);
  font-size: 0.75rem;
  line-height: 1rem;
  margin-bottom: 0.875rem;
}

.select {
  position: relative;
  min-width: 14.6875rem;
  padding: 1.125rem 1.4375rem;
  font-weight: 600;
  font-size: 0.875rem;
  line-height: calc(20 / 16);
  background-color: var(--default-lighter);
  border: none;
  border-radius: 1rem;
  transition: all 0.2s ease-in;
}

.select:hover {
  box-shadow: inset 0 0 0 2px var(--primary-opacity);
  background-color: #fff;
}

.select.focus {
  outline: none;
  background-color: white;
  box-shadow: inset 0 0 0 2px var(--primary);
}

.icon {
  position: absolute;
  top: 35%;
  right: 0;
  margin-right: 1.5rem;
  width: 0.75rem;
  height: fit-content;
  transform: rotate(-180deg);
  transition: all 0.2s;
}

.icon.focus {
  transform: rotate(0);
}

.options {
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0;
  list-style: none;
  padding: 0.875rem 2rem;
  background-color: white;
  border: 1px solid var(--default-lighter);
  border-radius: 1.5rem;
  box-shadow: 0 15px 27.1828px var(--shadow);
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s;
}

.option {
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
  line-height: calc(19 / 16);
  font-weight: 600;
  transition: all 0.1s ease-in;
}

.option:hover {
  background-color: var(--default-lighter);
}

.options.focus {
  opacity: 1;
  visibility: visible;
}

.text {
  color: var(--default);
}

.select.disabled {
  opacity: 0.5;
  cursor: no-drop;
}
